<template>
  <div class="goodsInfo-container">
    <!-- 头部导航栏 -->
    <van-nav-bar
      fixed
      placeholder
      title="商品列表"
      left-text="返回"
      left-arrow
      @click-left="$router.back()"
    />
    <!-- /头部导航栏 -->
    <!-- 轮播图 -->
    <van-image :src="this.goods_logo" />
    <!-- /轮播图 -->
    <div>
      <van-button type="info" round @click="buy">立即购买</van-button>
      <van-button type="danger" round @click="addToCart">加入购物车</van-button>
    </div>
    {{ count }}
    <van-stepper v-model="this.count" input-width="60px" button-size="50px" />

    <!-- <ul>
      <li v-for="(item, index) in $store.state.cartlist" :key="index">
        {{ item }}
      </li>
    </ul> -->
  </div>
</template>

<script>
import { getGoodsInfo } from '@/api/api'
import { mapActions } from 'vuex'

export default {
  name: 'goodsInfo',
  props: {},
  data() {
    return {
      iid: null,
      image_src: null,
      price: null,
      count: 1,
      goods_name: null,
      goods_logo: null
    }
  },
  created() {
    // 1.保存传入的id
    this.iid = this.$route.params.iid
    // 2.
    this.getGoodsInfo(this.iid)
  },
  methods: {
    ...mapActions(['addCart']),

    // 商品详情
    async getGoodsInfo() {
      const { message: res } = await getGoodsInfo(this.iid)
      // console.log(res.message)
      this.introduce = res.goods_introduce
      this.price = res.goods_price
      this.goods_name = res.goods_name
      this.goods_logo = res.goods_small_logo
    },
    // 添加到购物车
    addToCart() {
      // 1，获取购物车需要展示的信息
      const product = {}
      product.goods_id = this.iid
      product.goods_price = this.price
      product.goods_count = this.count
      product.goods_name = this.goods_name
      product.goods_logo = this.goods_logo

      // 2，将商品添加到购物车里
      // this.$store.cartList. push(product)
      // this.$store.commit('addCart', product)
      this.addCart(product).then(res => {
        this.$toast.success(res)
      })

      // this.$store.dispatch('addCart', product).then(res => {
      //   // 3.添加到购物车成功
      //   console.log(res)
      // })
    },
    buy() {
      this.$toast.success('假装购买成功')
    }
  }
}
</script>

<style scoped lang="less"></style>
